import React from 'react';
import styled from 'styled-components';
import { goBack } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { closeAppAction } from 'store/actions/app';
import { ButtonViews } from 'components/Button/Button';
import { DialogPage, DialogActionProps } from 'components/DialogPage/DialogPage';
import { layout } from 'style/const/layout';

import circlesImage from './Error.assets/circles.svg';
import warningIcon from './Error.assets/warning.svg';

const circlesPositionXPx = -266;
const circlesPositionYPx = -1 * (layout.headerHeightPx + 280);

const Circles = styled.div`
    position: absolute;
    top: ${circlesPositionYPx}px;
    right: ${circlesPositionXPx}px;

    box-sizing: border-box;
    width: 1596px;
    height: 1596px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url('${circlesImage}');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
`;

const WarningIcon = styled.div`
    width: 384px;
    height: 384px;

    background-image: url('${warningIcon}');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
`;

export const Error: React.FC = () => {
    const { title, subtitle, retryButtonText, closeButtonText } = useSelector(({ errorPage }) => errorPage);

    const dispatch = useDispatch();

    const handleRetryButtonClick = React.useCallback(() => {
        dispatch(goBack());
    }, [dispatch]);

    const handleCloseButtonClick = React.useCallback(() => {
        dispatch(closeAppAction());
    }, [dispatch]);

    const dialogActions = React.useMemo(() => {
        const result: DialogActionProps[] = [
            {
                shouldFocusOnMount: true,
                buttonView: ButtonViews.secondary,
                buttonText: retryButtonText,
                onClick: handleRetryButtonClick,
            },
        ];

        if (closeButtonText) {
            result.push({
                buttonView: ButtonViews.secondary,
                buttonText: closeButtonText,
                onClick: handleCloseButtonClick,
            });
        }

        return result;
    }, [retryButtonText, closeButtonText, handleRetryButtonClick, handleCloseButtonClick]);

    return (
        <DialogPage title={title} text={subtitle} dialogActions={dialogActions}>
            <Circles>
                <WarningIcon />
            </Circles>
        </DialogPage>
    );
};
