import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { closeAppAction } from 'store/actions/app';
import { ButtonViews } from 'components/Button/Button';
import { DialogPage, DialogActionProps } from 'components/DialogPage/DialogPage';
import { layout } from 'style/const/layout';

import backgroundImage from './Status.assets/bg.png';

const StyledBackground = styled.div`
    position: absolute;
    top: ${-1 * layout.headerHeightPx}px;
    left: 0;

    width: ${layout.widthPx}px;
    height: ${layout.heightPx}px;

    background-image: url('${backgroundImage}');
    background-size: contain;
    background-repeat: no-repeat;
`;

export const Status: React.FC = () => {
    const dispatch = useDispatch();
    const { deliveryTime } = useSelector(({ delivery }) => delivery);

    const dialogActions = React.useMemo(
        (): DialogActionProps[] => [
            {
                shouldFocusOnMount: true,
                buttonText: 'Очень жду',
                buttonView: ButtonViews.primary,
                onClick: () => {
                    dispatch(closeAppAction());
                },
            },
        ],
        [dispatch],
    );

    return (
        <DialogPage title={`Заказ принят. \nБудем у вас через \n${deliveryTime} минут.`} dialogActions={dialogActions}>
            <StyledBackground />
        </DialogPage>
    );
};
