import React from 'react';
import styled from 'styled-components';
import { ButtonViews, Button, ButtonSize } from 'components/Button/Button';
import { HeadlineText1 } from 'components/Text/Headline';
import { BodyText1, BodyText2 } from 'components/Text/Body';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout';

const StyledContainer = styled.div`
    position: relative;

    height: ${layout.contentHeightPx}px;
    padding-left: 128px;
`;

const StyledContent = styled.div`
    position: relative;
    width: 816px;
    height: ${layout.contentHeightPx}px;
    z-index: 2;

    display: flex;
    flex-direction: column;
    justify-content: center;

    align-content: left;
`;

const StyledTitle = styled(HeadlineText1)`
    color: ${color.text.primary};
`;

const StyledText = styled(BodyText1)`
    margin-top: 24px;

    min-height: 72px;

    white-space: pre-wrap;

    color: ${color.text.secondary};
`;

const StyledButtonContainer = styled.div`
    margin-left: -24px;
    margin-top: 64px;

    display: flex;
    flex-direction: row;
`;

const StyledButton = styled(Button)`
    margin: 0 24px;

    padding: 20px 32px;
`;

const StyledButtonText = styled(BodyText2)``;

const StyledDecorations = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
`;

export interface DialogActionProps {
    shouldFocusOnMount?: boolean;
    buttonText: string;
    buttonView: ButtonViews;
    onClick: (event: React.SyntheticEvent<HTMLButtonElement>) => void;
}

interface GenericStatusPageProps {
    title: string;
    text?: string;
    buttonSize?: ButtonSize;
    dialogActions?: DialogActionProps[];
}
const DialogActionButton = ({
    shouldFocusOnMount,
    buttonText,
    buttonView,
    buttonSize,
    onClick,
}: DialogActionProps & { buttonSize: ButtonSize }) => (
    <StyledButton shouldFocusOnMount={shouldFocusOnMount} view={buttonView} size={buttonSize} onClick={onClick}>
        <StyledButtonText>{buttonText}</StyledButtonText>
    </StyledButton>
);

export const DialogPage: React.FC<GenericStatusPageProps> = ({
    title,
    text,
    dialogActions,
    buttonSize = ButtonSize.s,
    children,
}) => {
    const buttonList = dialogActions?.map((buttonProps, index) => (
        <DialogActionButton key={index} buttonSize={buttonSize} {...buttonProps} />
    ));

    return (
        <StyledContainer>
            <StyledContent>
                <StyledTitle>
                    <MultilineText text={title} />
                </StyledTitle>
                {text && (
                    <StyledText>
                        <MultilineText text={text} />
                    </StyledText>
                )}
                {buttonList && <StyledButtonContainer>{buttonList}</StyledButtonContainer>}
            </StyledContent>
            {children && <StyledDecorations>{children}</StyledDecorations>}
        </StyledContainer>
    );
};
